<template>
	<v-form v-model="valid" ref="form">
		<span class="subtitle-1">E-mail</span>
		<v-text-field
			outlined
			dense
			v-model="dataRegister.email"
			:rules="emailRule"
		></v-text-field>
		<span class="subtitle-1">Password</span>
		<v-text-field
			:append-icon="showpassword ? 'mdi-eye' : 'mdi-eye-off'"
      :type="showpassword ? 'text' : 'password'"
      @click:append="showpassword = !showpassword"
			outlined
			dense
			v-model="dataRegister.password"
			:rules="requiredRule"
		></v-text-field>
		<Notif :msg="msg" :status="status" @visible="visible" />
		<div class="d-flex">
			<v-btn
				depressed
				:loading="isLoading"
				color="purple"
				type="submit"
				dark
				@click.prevent="validate">
				Register
			</v-btn>
			<v-spacer></v-spacer>
			<div class="mt-2">
				Have an account ? 
				<span 
					class="pointer purple--text"
					@click="$router.push({ name: 'login-incubator' })">
					Login
				</span>
			</div>
		</div>
	</v-form>
</template>

<script>
	import { mapState } from 'vuex'
	import Notif from '@/components/Notif'

	export default{
		data: () => ({
			valid: true,
			dataRegister: {
				email: '',
				password: ''
			},
			requiredRule: [ v => !!v || 'Data harus diisi' ],
			msg: {
				success: 'Register berhasil',
				error: 'Register gagal',
				visible: false
			},
			showpassword: false
		}),

		components: { Notif },

		computed: {
			...mapState('dataRegister', ['isLoading', 'status']),
			emailRule () {
				return [
          ...this.requiredRule,
          v => /.+@.+\..+/.test(v) || 'Data berupa e-mail'
        ]
			}
		},

		methods: {
			validate () {
				if (this.$refs.form.validate()) {
					this.$store.dispatch('dataRegister/postRegisterIncubator', this.dataRegister)
					this.$store.dispatch('dataRegister/updateIsLoading', true)
					this.msg.visible = true
				}
			},
			visible(val) {
				this.msg.visible = val
				this.$store.commit('dataRegister/updateStatus', null)
			}
		}
	}
</script>